___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color: rgb(157, 0, 255);
    --primary-color-darker: rgb(43, 5, 67);
    --primary-color-dark: rgb(98, 29, 140);
    --primary-color-red: rgb(223, 12, 48);
    --primary-color-white: #fff;
}

* {
    box-sizing: border-box;
    outline: 0;

}

body {
    margin: 0;
    padding: 0;
    background: var(--primary-color);
    font-family: 'Open snas', sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
}

.container {
    max-width: 640px;
    margin: 50px auto;
    background: var(--primary-color-white);
    padding: 20px;
    border-radius: 20px;

}

.input-button-container {
    display: flex; /* Utiliza flexbox para alinhar os elementos lado a lado */
    align-items: center; /* Centraliza verticalmente */
}

input[type="number"]{
    height: 100%;
    width: 18%;
}

input[type="checkbox"]{
    height: 25px;
    width: 25px;
}

input {
    font-size: 24px;
    padding: 0 20px;
    border-radius: 10px;
    margin: 10px;
}

input:focus{
    outline: 1px solid var(--primary-color);
}

button {
    border: none;
    cursor: pointer;
    background: var(--primary-color);
    color: var(--primary-color-white);
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    width: 50%;
    margin-top: 30px;
    margin-bottom: 10px;
    border-radius: 10px;
}

button:hover {
    background: var(--primary-color-darker);
}

button:active {
    background: var(--primary-color-dark);
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AAEA;IACI,iCAAiC;IACjC,sCAAsC;IACtC,sCAAsC;IACtC,qCAAqC;IACrC,2BAA2B;AAC/B;;AAEA;IACI,sBAAsB;IACtB,UAAU;;AAEd;;AAEA;IACI,SAAS;IACT,UAAU;IACV,gCAAgC;IAChC,oCAAoC;IACpC,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;IACjB,sCAAsC;IACtC,aAAa;IACb,mBAAmB;;AAEvB;;AAEA;IACI,aAAa,EAAE,0DAA0D;IACzE,mBAAmB,EAAE,6BAA6B;AACtD;;AAEA;IACI,YAAY;IACZ,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,eAAe;IACf,eAAe;IACf,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,gCAAgC;IAChC,iCAAiC;IACjC,eAAe;IACf,gBAAgB;IAChB,YAAY;IACZ,UAAU;IACV,gBAAgB;IAChB,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,qCAAqC;AACzC","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');\r\n\r\n:root {\r\n    --primary-color: rgb(157, 0, 255);\r\n    --primary-color-darker: rgb(43, 5, 67);\r\n    --primary-color-dark: rgb(98, 29, 140);\r\n    --primary-color-red: rgb(223, 12, 48);\r\n    --primary-color-white: #fff;\r\n}\r\n\r\n* {\r\n    box-sizing: border-box;\r\n    outline: 0;\r\n\r\n}\r\n\r\nbody {\r\n    margin: 0;\r\n    padding: 0;\r\n    background: var(--primary-color);\r\n    font-family: 'Open snas', sans-serif;\r\n    font-size: 1.3em;\r\n    line-height: 1.5em;\r\n}\r\n\r\n.container {\r\n    max-width: 640px;\r\n    margin: 50px auto;\r\n    background: var(--primary-color-white);\r\n    padding: 20px;\r\n    border-radius: 20px;\r\n\r\n}\r\n\r\n.input-button-container {\r\n    display: flex; /* Utiliza flexbox para alinhar os elementos lado a lado */\r\n    align-items: center; /* Centraliza verticalmente */\r\n}\r\n\r\ninput[type=\"number\"]{\r\n    height: 100%;\r\n    width: 18%;\r\n}\r\n\r\ninput[type=\"checkbox\"]{\r\n    height: 25px;\r\n    width: 25px;\r\n}\r\n\r\ninput {\r\n    font-size: 24px;\r\n    padding: 0 20px;\r\n    border-radius: 10px;\r\n    margin: 1